.list{
    color:white;
    width: 300px;
    background-color: rgb(165, 20, 179);;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}
.list p{
    padding:10px;
    position: relative;
}
.list p span{
    position:absolute;
    right: 10px;
    cursor: pointer;
}
.faicons{
    margin: 0 5px;
    font-size: 15px;
}